import { ref, watch, provide, Teleport, computed, onMounted, Transition, onActivated, onDeactivated, defineComponent, } from 'vue';
// Utils
import { popupSharedProps } from './shared';
import { createNamespace, extend, isDef } from '../utils';
import { callInterceptor } from '../utils/interceptor';
// Composables
import { useEventListener } from '@vant/use';
import { useExpose } from '../composables/use-expose';
import { useLockScroll } from '../composables/use-lock-scroll';
import { useLazyRender } from '../composables/use-lazy-render';
import { POPUP_TOGGLE_KEY } from '../composables/on-popup-reopen';
// Components
import { Icon } from '../icon';
import { Overlay } from '../overlay';
const [name, bem] = createNamespace('popup');
let globalZIndex = 2000;
export default defineComponent({
    name,
    inheritAttrs: false,
    props: extend({}, popupSharedProps, {
        round: Boolean,
        closeable: Boolean,
        transition: String,
        iconPrefix: String,
        closeOnPopstate: Boolean,
        safeAreaInsetBottom: Boolean,
        position: {
            type: String,
            default: 'center',
        },
        closeIcon: {
            type: String,
            default: 'cross',
        },
        closeIconPosition: {
            type: String,
            default: 'top-right',
        },
    }),
    emits: [
        'open',
        'close',
        'click',
        'opened',
        'closed',
        'update:show',
        'click-overlay',
        'click-close-icon',
    ],
    setup(props, { emit, attrs, slots }) {
        let opened;
        let shouldReopen;
        const zIndex = ref();
        const popupRef = ref();
        const lazyRender = useLazyRender(() => props.show || !props.lazyRender);
        const style = computed(() => {
            const style = {
                zIndex: zIndex.value,
            };
            if (isDef(props.duration)) {
                const key = props.position === 'center'
                    ? 'animationDuration'
                    : 'transitionDuration';
                style[key] = `${props.duration}s`;
            }
            return style;
        });
        const open = () => {
            if (!opened) {
                if (props.zIndex !== undefined) {
                    globalZIndex = +props.zIndex;
                }
                opened = true;
                zIndex.value = ++globalZIndex;
                emit('open');
            }
        };
        const triggerClose = () => {
            opened = false;
            emit('close');
            emit('update:show', false);
        };
        const close = () => {
            if (opened) {
                callInterceptor({
                    interceptor: props.beforeClose,
                    done: triggerClose,
                });
            }
        };
        const onClickOverlay = (event) => {
            emit('click-overlay', event);
            if (props.closeOnClickOverlay) {
                close();
            }
        };
        const renderOverlay = () => {
            if (props.overlay) {
                return (<Overlay v-slots={{ default: slots['overlay-content'] }} show={props.show} class={props.overlayClass} zIndex={zIndex.value} duration={props.duration} customStyle={props.overlayStyle} onClick={onClickOverlay}/>);
            }
        };
        const onClickCloseIcon = (event) => {
            emit('click-close-icon', event);
            close();
        };
        const renderCloseIcon = () => {
            if (props.closeable) {
                return (<Icon role="button" tabindex={0} name={props.closeIcon} class={bem('close-icon', props.closeIconPosition)} classPrefix={props.iconPrefix} onClick={onClickCloseIcon}/>);
            }
        };
        const onClick = (event) => emit('click', event);
        const onOpened = () => emit('opened');
        const onClosed = () => emit('closed');
        const renderPopup = lazyRender(() => {
            const { round, position, safeAreaInsetBottom } = props;
            return (<div v-show={props.show} ref={popupRef} style={style.value} class={bem({
                round,
                [position]: position,
                'safe-area-inset-bottom': safeAreaInsetBottom,
            })} onClick={onClick} {...attrs}>
          {slots.default?.()}
          {renderCloseIcon()}
        </div>);
        });
        const renderTransition = () => {
            const { position, transition, transitionAppear } = props;
            const name = position === 'center' ? 'van-fade' : `van-popup-slide-${position}`;
            return (<Transition name={transition || name} appear={transitionAppear} onAfterEnter={onOpened} onAfterLeave={onClosed}>
          {renderPopup()}
        </Transition>);
        };
        watch(() => props.show, (value) => {
            if (value) {
                open();
            }
            else {
                close();
            }
        });
        useExpose({ popupRef });
        useLockScroll(popupRef, () => props.show && props.lockScroll);
        useEventListener('popstate', () => {
            if (props.closeOnPopstate) {
                close();
                shouldReopen = false;
            }
        });
        onMounted(() => {
            if (props.show) {
                open();
            }
        });
        onActivated(() => {
            if (shouldReopen) {
                emit('update:show', true);
                shouldReopen = false;
            }
        });
        onDeactivated(() => {
            if (props.show) {
                close();
                shouldReopen = true;
            }
        });
        provide(POPUP_TOGGLE_KEY, () => props.show);
        return () => {
            if (props.teleport) {
                return (<Teleport to={props.teleport}>
            {renderOverlay()}
            {renderTransition()}
          </Teleport>);
            }
            return (<>
          {renderOverlay()}
          {renderTransition()}
        </>);
        };
    },
});
